<template>
  <div class="flex flex-column gap-3">
    <Panel header="Фильтр" toggleable :collapsed="true" class="w-full">
      <!-- <div class="form-grid grid">
        <div class="field col">
          <label for="dateCreate">Дата создания</label>
          <Calendar
            id="dateCreate"
            v-model="datesCreate"
            selectionMode="range"
            :manualInput="false"
          />
        </div>

        <div class="field col">
          <label for="firstname2">Лицевой счет</label>
          <input
            id="firstname2"
            type="text"
            class="text-base text-color surface-overlay p-2 border-1 border-solid surface-border border-round appearance-none outline-none focus:border-primary w-full"
          />
        </div>

        <div class="field col">
          <label for="lastname2">IP - адрес узла</label>
          <input
            id="lastname2"
            type="text"
            class="text-base text-color surface-overlay p-2 border-1 border-solid surface-border border-round appearance-none outline-none focus:border-primary w-full"
          />
        </div>
        <div class="field col">
          <label for="lastname2">Участок связи</label>
          <input
            id="lastname2"
            type="text"
            class="text-base text-color surface-overlay p-2 border-1 border-solid surface-border border-round appearance-none outline-none focus:border-primary w-full"
          />
        </div>
        <div class="field col mt-3 align-content-end justify-content-end">
          <Button label="Показать" />
        </div>
      </div> -->
      <div
        class="flex flex-wrap align-items-center justify-content-start gap-2"
      >
        <div class="flex flex-column flex-nowrap gap-2">
          <div>Дата создания</div>
          <Calendar
            v-model="datesCreate"
            selectionMode="range"
            :manualInput="false"
            placeholder="Дата создания"
            class="w-full md:w-14rem"
          />
        </div>

        <div class="flex flex-column flex-nowrap gap-2">
          <div>Дата закрытия</div>
          <Calendar
            v-model="datesClosed"
            selectionMode="range"
            :manualInput="false"
            placeholder="Дата закрытия"
            class="w-full md:w-14rem"
          />
        </div>

        <div class="flex flex-column flex-nowrap gap-2">
          <div>Лицевой счет</div>
          <InputText
            v-model="personAccount"
            placeholder="Лицевой счет"
            class="w-full md:w-14rem"
          />
        </div>

        <div class="flex flex-column flex-nowrap gap-2">
          <div>Город</div>
          <Dropdown
            v-model="selectedCity"
            :options="spr.cityOptions.value"
            optionLabel="NAME"
            optionValue="ID"
            class="w-full md:w-14rem"
          />
        </div>

        <div class="flex flex-column flex-nowrap gap-2">
          <div>Статус</div>
          <Dropdown
            v-model="statusSelected"
            :options="statuses"
            optionLabel="name"
            optionValue="id"
            class="w-full md:w-14rem"
          />
        </div>
      </div>

      <template #footer>
        <div
          class="flex flex-wrap align-items-center justify-content-end gap-3"
        >
          <Button text label="Очистить" @click="clearFilter()" />
          <Button
            label="Показать"
            @click="searchEntity()"
            @keydown.enter="searchEntity()"
          />
        </div>
      </template>
    </Panel>

    <DataTable
      v-model:selection="selectedRows"
      :sortOrder="-1"
      :value="spr.tableCheckIp.value"
      dataKey="ID"
      paginator
      :rows="10"
      :rowsPerPageOptions="[5, 10, 20, 50]"
      tableStyle="min-width: 60rem"
    >
      <Column selectionMode="multiple" headerStyle="width: 3rem"></Column>
      <Column field="DATE_CREATE">
        <template #header>
          <div>Дата</div>
        </template>
        <template #body="slotProps">
          {{ slotProps.data.DATE_CREATE }}
        </template>
      </Column>
      <Column field="PERSONAL_ACCOUNT">
        <template #header>
          <div>Лицевой счет</div>
        </template>
        <template #body="slotProps">
          <span
            class="elem_pointer"
            @click="
              openLk(slotProps.data.LK_ID, slotProps.data.PERSONAL_ACCOUNT)
            "
          >
            {{ slotProps.data.PERSONAL_ACCOUNT }}</span
          >
        </template>
      </Column>

      <Column field="PESONAL_DATA">
        <template #header>
          <div>Ф.И.О</div>
        </template>
        <template #body="slotProps">
          <span
            class="elem_pointer"
            @click="
              openClient(
                slotProps.data.LK_COMPANY_ID,
                slotProps.data.PESONAL_DATA
              )
            "
          >
            {{ slotProps.data.PESONAL_DATA }}</span
          >
        </template>
      </Column>
      <Column field="LK_CITY">
        <template #header>
          <div>Город</div>
        </template>
        <template #body="slotProps">
          {{
            slotProps.data.LK_CITY ? spr.city.value[slotProps.data.LK_CITY] : ""
          }}
        </template>
      </Column>

      <Column field="IP_ADDRESS">
        <template #header>
          <div>IP-адрес</div>
        </template>
        <template #body="slotProps">
          {{ slotProps.data.IP_ADDRESS }}
        </template>
      </Column>
      <Column field="PORT">
        <template #header>
          <div>Порт</div>
        </template>
        <template #body="slotProps">
          {{ slotProps.data.PORT }}
        </template>
      </Column>
      <Column field="GATEWAY">
        <template #header>
          <div>Шлюз</div>
        </template>
        <template #body="slotProps">
          {{ slotProps.data.GATEWAY }}
        </template>
      </Column>
      <Column field="STATUS_ID">
        <template #header>
          <div>Статус</div>
        </template>
        <template #body="slotProps">
          {{ statusesGet[slotProps.data.STATUS_ID] }}
        </template>
      </Column>
      <template #footer>
        <div class="flex flex-row flex-nowrap gap-2">
          <span>Выбирете действие:</span>
          <Dropdown
            v-model="selectedOptions"
            :options="options"
            optionLabel="name"
            placeholder="Действие"
            class="w-full md:w-14rem"
          ></Dropdown>
          <Button @click="executeAction()">Выполнить</Button>
        </div>
      </template>
    </DataTable>
  </div>
  <Toast position="top-right" group="tr" />
</template>

<script setup>
import { storeProactive } from "@/store/storeProactive";
import { storeToRefs } from "pinia";
import { onMounted, ref } from "vue";
import { useToast } from "primevue/usetoast";
import moment from "moment";
const toast = useToast();
const sp = storeProactive();
const spr = storeToRefs(sp);
const selectedRows = ref([]);
const selectedOptions = ref();
const datesCreate = ref();
const datesClosed = ref();
const personAccount = ref();
const selectedCity = ref();
const statusSelected = ref();

const options = ref([
  { id: 1, name: "Удалить" },
  { id: 2, name: "Освободить" },
]);
const statuses = ref([
  { id: 1, name: "Новая" },
  { id: 2, name: "Освобожденно" },
]);

const statusesGet = ref([]);

const openClient = (clientId, person) => {
  if (clientId) {
    BX.SidePanel.Instance.open(" /crm/company/details/" + clientId + "/");
  } else {
    toast.add({
      severity: "info",
      summary: "В базе не найден клиент:",
      detail: person,
      group: "tr",
      life: 3000,
    });
  }
};

const openLk = (openLk, lk) => {
  if (openLk) {
    BX.SidePanel.Instance.open(
      "/crm/type/" +
        spr.settings.value.smartPersonalAccount.ID +
        "/details/" +
        openLk +
        "/"
    );
  } else {
    toast.add({
      severity: "info",
      summary: "В базе не найден Лицевой счет:",
      detail: lk,
      group: "tr",
      life: 3000,
    });
  }
};
const clearFilter = () => {
  datesCreate.value = null;
  datesClosed.value = null;
  personAccount.value = null;
  selectedCity.value = null;
  statusSelected.value = null;
};
const searchEntity = async () => {
  var filter = [];
  if (datesCreate.value) {
    filter.push({
      ">=DATE_CREATE":
        moment(datesCreate.value[0]).format("DD.MM.YYYY") + " 00:00:00",
    });
    filter.push({
      "<=DATE_CREATE":
        moment(datesCreate.value[1]).format("DD.MM.YYYY") + " 23:59:59",
    });
  }
  if (datesClosed.value) {
    filter.push({
      ">=DATE_CLOSED":
        moment(datesClosed.value[0]).format("DD.MM.YYYY") + " 00:00:00",
    });
    filter.push({
      "<=DATE_CLOSED":
        moment(datesClosed.value[1]).format("DD.MM.YYYY") + " 23:59:59",
    });
  }

  if (selectedCity.value) {
    filter.push({ "LK.UF_CRM_5_CITY_LIST": selectedCity.value });
  }

  if (personAccount.value) {
    filter.push({ "LK.UF_CRM_5_LS_NUMBER": personAccount.value });
  }

  if (statusSelected.value) {
    filter.push({ STATUS_ID: statusSelected.value });
  }

  console.log(filter);
  await sp.getCheckIp(filter);
};

const executeAction = async () => {
  if (selectedOptions.value.id == 2) {
    const complite = await new Promise((resolve) => {
      BX.rest.callMethod(
        "mwsproactiverest.ipcheck.ipReleases",
        {
          elements: selectedRows.value,
        },
        (res) => {
          resolve(res.data());
        }
      );
    });
    if (complite == "OK") {
      selectedRows.value = [];
      await searchEntity();
    }
  } else {
    toast.add({
      severity: "info",
      summary: "Удаление пока не реализованно:",
      detail: "Неизвестно нужно ли оно или нет",
      group: "tr",
      life: 3000,
    });
  }
};

onMounted(async () => {
  await sp.getCheckIp();
  statuses.value.forEach((element) => {
    statusesGet.value[element.id] = element.name;
  });
});
</script>

<style lang="scss" scoped>
.elem_pointer {
  cursor: pointer;
}
</style>
