import { defineStore } from "pinia/dist/pinia";

export const storeProactive = defineStore("storeProactive", {
  state: () => {
    return {
      metrics: [],
      unitsTable: [],
      status: [],
      tableCheckIp: [],
      settings: {
        categoryMain: null,
        categoryKC: null,
        categoryAO: null,
        smartPersonalAccount: null,
        listCity: null,
        cityDay: [],
        defaultDay: 0,
        dealField: {
          engineer: null,
          technicist: null,
        },
        smartField: {
          personalAccount: null,
          address: null,
          city: null,
          asigned: null,
        },
      },
      city: [],
      cityOptions: [],
    };
  },
  actions: {
    async getSettings() {
      const checkSetting = await new Promise((resolve) => {
        BX.rest.callMethod(
          "mwsproactiverest.setting.getList",
          {
            filter: {
              NAME: "PROACTIVE",
            },
            select: ["*"],
          },
          (res) => {
            resolve(res.data());
          }
        );
      });

      if (checkSetting.length > 0) {
        if (checkSetting[0].SETTING_STRING) {
          const obj = JSON.parse(checkSetting[0].SETTING_STRING);

          this.settings = {
            categoryMain: obj.categoryMain ? obj.categoryMain : null,
            categoryKC: obj.categoryKC ? obj.categoryKC : null,
            categoryAO: obj.categoryAO ? obj.categoryAO : null,
            smartPersonalAccount: obj.smartPersonalAccount
              ? obj.smartPersonalAccount
              : null,
            listCity: obj.listCity ? obj.listCity : null,
            cityDay: obj.cityDay ? obj.cityDay : [],
            defaultDay: obj.defaultDay ? obj.defaultDay : 0,
            dealField: {
              engineer:
                obj.dealField && obj.dealField.engineer
                  ? obj.dealField.engineer
                  : null,
              technicist:
                obj.dealField && obj.dealField.technicist
                  ? obj.dealField.technicist
                  : null,
            },
            smartField: {
              personalAccount:
                obj.smartField && obj.smartField.personalAccount
                  ? obj.smartField.personalAccount
                  : null,
              address:
                obj.smartField && obj.smartField.address
                  ? obj.smartField.address
                  : null,
              city:
                obj.smartField && obj.smartField.city
                  ? obj.smartField.city
                  : null,
              asigned:
                obj.smartField && obj.smartField.asigned
                  ? obj.smartField.asigned
                  : null,
            },
          };
        }
        console.log(this.settings);
      }
    },
    async loadStatusProactive() {
      const stat = await new Promise((resolve) => {
        BX.rest.callMethod(
          "mwsproactiverest.abonentStatus.getList",
          { select: ["*"] },
          (res) => {
            resolve(res.data());
          }
        );
      });
      this.status = stat;
    },
    async loadMainTable() {
      const mainTable = await new Promise((resolve) => {
        BX.rest.callMethod("mwsproactive.ui.proactiveTableUi", {}, (res) => {
          resolve(res.data());
        });
      });

      this.metrics = mainTable;
    },
    async loadUnits(abonentId, metric) {
      this.unitsTable = [];
      const units = await new Promise((resolve) => {
        BX.rest.callMethod(
          "mwsproactiverest.metricunit.getList",
          {
            filter: {
              ABONENT_ID: abonentId,
              METRIC_ASUO: metric,
            },
            select: ["*"],
          },
          (res) => {
            resolve(res.data());
          }
        );
      });
      this.unitsTable = units;
    },
    async createDeal() {},
    async getCheckIp(filter) {
      const checkIp = await new Promise((resolve) => {
        BX.rest.callMethod(
          "mwsproactiverest.ipcheck.getList",
          {
            enID: this.settings.smartPersonalAccount.ID,
            filter: filter ? filter : [],
          },
          (res) => {
            resolve(res.data());
          }
        );
      });
      this.tableCheckIp = checkIp;
    },
    async getCity() {
      const city = await new Promise((resolve) => {
        BX.rest.callMethod(
          "mwsproactiverest.list.getElements",
          { ID: this.settings.listCity.ID },
          (res) => {
            resolve(res.data());
          }
        );
      });
      city.forEach((element) => {
        this.city[element.ID] = element.NAME;
      });
      this.cityOptions = city;
    },
  },
  getters: {
    getMetrics(state) {
      return state.metrics;
    },
    getUnitsTable(state) {
      return state.unitsTable;
    },
    getStatus(state) {
      return state.status;
    },
  },
});
